import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'newsType'
})
export class NewsTypePipe implements PipeTransform {

  transform(value: string): any {
    switch (value) {
      case 'dept':
        return '';
      case 'speech':
        return '';
      case 'other':
        return '';
      case 'imexam':
        return '';
      case 'phdexam':
        return '';
    }
  }

}



